<template>
  <div class="container-body desktopInput">
    <div class="input-box">
      <div class="page-title">
      <span>
        اگه مدل دستگاهت رو میدونی برامون بنویس
      </span>
      </div>
      <text-box txtMode="primary" class="txt" mode="primary" v-model="deviceModel" placeholder="درج مدل دستگاه"/>
    </div>
    <div class="action-btn">
      <btn class="btn" mode="outline-gray" @click="next()">بعدی</btn>
      <btn class="btn" mode="outline-gray" @click="prev()">قبلی</btn>
    </div>
  </div>
</template>

<script>
import RadioBox from "@/components/RadioBox";
import router from "@/router";
import TextBox from "@/components/TextBox";

export default {
  name: "deviceModel",
  data() {
    return {
      MySelectedValue: "",
      deviceModel: ""
    }
  },
  components: {
    TextBox,
    RadioBox,
  },
  methods: {
    next() {
      this.$store.commit('setDeviceMark', this.deviceModel)
      this.$router.push('packageIssue')
    },
    prev() {
      this.$router.go(-1)
    },
  }
}
</script>

