<template>
  <div>
    <div class="title" :class="[{disabledTitle: this.disabled},{addressTitle: isAddress}]">
      <div>{{ title }}</div>
      <div class="editAddress" v-if="isAddress">ویرایش</div>
<!--      <div></div>-->
    </div>
    <div :class="[`container-${style}`, {disabledTxtBox: this.disabled}, {address: isAddress}]">
      <input type="text" :value="value" @input="input" :class="style" @click="btnAction"
             :placeholder="placeholder" :disabled="disabled" :maxlength="maxlength"/>
      <btn
          mode="primary"
          :class="{disabledBtn: this.disabledTextBoxBtn}"
          class="btn" v-if="hasBtn"
          :disabled="disabled"
          @click="clickBtn()"
      >
        {{ hasBtnTitle }}
      </btn>
    </div>
  </div>
</template>

<script>
import Btn from "@/components/Btn";

export default {
  name: "TextBox",
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String
    },
    disabledTextBoxBtn: {
      type: Boolean,
      default: false,
    },
    txtMode: {
      type: String,
      default: 'secondary',
    },
    title: {
      type: String,
      required: false
    },
    hasBtn: {
      type: String,
      default: "",
      required: false,
    },
    value: {},
    placeholder: {
      type: String,
      default: '...',
    },
  },
  components: {
    Btn
  },
  mounted() {

  },
  methods: {
    btnAction() {
      this.$emit('click')
    },
    clickBtn() {
      if (!this.disabledTextBoxBtn) this.$emit('clickBtn')
    },
    input(e) {
      // if (this.type === 'mobile') {
      //   let result = /09[0-3][0-9]-?[0-9]{3}-?[0-9]{4}/.test(e.target.value)
      //   if (result) {
      //       // return;
      //   }
      // }
      this.$emit('input', e.target.value)
    }
  },
  data() {
    return {
      text: 'test',
      btnTitle: "",

    }
  },
  computed: {
    isAddress() {
      return this.title === "آدرس"
    },
    maxlength() {
      return this.type === 'mobile' ? 11 : false
    },
    hasBtnTitle() {
      return !!this.hasBtn ? this.hasBtn : ""
    },
    style() {
      if (this.txtMode == 'primary') return 'txt1';
      if (this.txtMode == 'secondary') return 'txt2';
    }
  },
}
</script>

