.editAddress {
  cursor: pointer;
}

.addressTitle {
  display: flex;
  justify-content: space-between;
  color: #016BB5;
}

.btn {
  height: 48px;
  font-weight: 500;
  font-size: 12px;
  width: 50%;
  //max-width: 8rem;
  border-radius: 6px;
  margin-left: 0.025rem;
}

.container-txt1 {
  margin-top: 0.5rem;
  height: 42px;
  border: 1.5px solid #666666;
  border-radius: 0.25rem;
  display: flex;

  .txt1 {
    all: unset;
    direction: rtl;
    width: 100%;
    border: none;
    background-color: transparent;
    text-indent: 1rem;
  }
}

.address {
  height: 89px !important;
}

.container-txt2 {
  margin-top: 0.5rem;
  height: 48px;
  border: 1.5px solid #E5E5E5;
  color: #262626;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;

  .txt2 {
    all: unset;
    direction: rtl;
    width: 100%;
    border: none;
    background-color: transparent;
    text-indent: 1rem;
  }
}

.disabledTitle {
  color: #CCCCCC;
}

.disabledTxtBox {
  background-color: #F5F5F5;
}

.disabledBtn {
  background-color: #E5E5E5;
  &:hover {
    background-color: #E5E5E5;
  }
}

</style>